}

/* Estilo para o formul√°rio de login */
.formulario-login {
    display: flex;                  /* Configura√ß√£o de exibi√ß√£o flex√≠vel para os itens do cont√™iner */
    flex-direction: column;         /* Organiza√ß√£o dos itens em coluna */
    justify-content: center;        /* Centraliza√ß√£o vertical dos itens */
    align-items: center;            /* Centraliza√ß√£o horizontal dos itens */
}

/* Estilo para o formul√°rio de login - largura */
.formulario-login_form {
    width: 40%;                     /* Largura do formul√°rio de login de 40% */
}

/* Estilo para o t√≠tulo do formul√°rio de login */
.formulario-login__titulo {
    margin-bottom: 3rem;            /* Margem inferior de 3 unidades de medida */
    font-weight: 800;               /* Peso da fonte 800 (negrito) */
