}

/* Estilo para r√≥tulos de formul√°rio */
.form__label {
    margin-bottom: 1rem;            /* Margem inferior de 1 unidade de medida */
}

/* Container para os elementos de entrada do formul√°rio de login */
.formulario-login__input-container {
	position: relative;                /* Define a posi√ß√£o do cont√™iner em rela√ß√£o ao elemento pai */
	margin-bottom: 1rem;               /* Adiciona uma margem na parte inferior do cont√™iner */
	display: flex;                     /* Configura√ß√£o de exibi√ß√£o flex√≠vel para os itens do cont√™iner */
	flex-direction: column;            /* Organiza√ß√£o dos itens em coluna */
	justify-content: flex-end;         /* Alinha os itens na extremidade inferior do cont√™iner */
	box-sizing: border-box;            /* Configura a caixa para incluir bordas e preenchimento no tamanho total */
}

/* Estilo para o campo de entrada de texto */
.input {
	background-color: var(--branco);   /* Cor de fundo personalizada para o campo de entrada */
	border: none;                      /* Remove a borda padr√£o do campo de entrada */
	box-sizing: border-box;            /* Configura a caixa para incluir bordas e preenchimento no tamanho total */
	border-bottom: 1px solid var(--cor-de-destaque);  /* Adiciona uma borda na parte inferior */
	padding: 1.375rem 0.5rem 0.5rem;    /* Adiciona preenchimento ao campo de entrada */
	height: 3.25rem;                   /* Define a altura do campo de entrada */
	width: 100%;                       /* Largura total do campo de entrada */
}

/* Estilo para o texto de espa√ßo reservado no campo de entrada */
.input::placeholder {
	visibility: hidden;                /* Esconde o texto de espa√ßo reservado no campo de entrada */
	color: var(--cor-de-realce);       /* Cor do texto de espa√ßo reservado personalizada */
}

/* Estilo para o campo de entrada quando est√° em foco */
.input:focus {
    outline: none;                     /* Remove a borda de contorno ao focar no campo de entrada */
}

/* Estilo para o r√≥tulo do campo de entrada */
.input-label {
	position: absolute;                /* Define a posi√ß√£o absoluta do r√≥tulo em rela√ß√£o ao cont√™iner pai */
	top: 1.375rem;                     /* Afasta o r√≥tulo da parte superior do campo de entrada */
	left: 0.5rem;                      /* Afasta o r√≥tulo da parte esquerda do campo de entrada */

	transition: all 0.25s;             /* Adiciona uma transi√ß√£o suave de 0.25 segundos para altera√ß√µes */
}

  
/* Estilo para o r√≥tulo do campo de entrada quando o campo n√£o est√° vazio */
.input:not(:placeholder-shown) + .input-label,
/* Estilo para o r√≥tulo do campo de entrada quando est√° em foco */
.input:focus + .input-label {
	font-size: 0.875rem;                /* Reduz o tamanho da fonte do r√≥tulo */
	top: 0.25rem;                       /* Ajusta a posi√ß√£o do r√≥tulo para cima */
	transition: all 0.25s;              /* Adiciona uma transi√ß√£o suave de 0.25 segundos para altera√ß√µes */
}

/* Estilo para o cont√™iner do campo de entrada quando h√° um erro de valida√ß√£o */
.input-container--invalid {
	margin-bottom: 0.5rem;               /* Adiciona uma margem na parte inferior do cont√™iner */
}

/* Estilo para o campo de entrada quando h√° um erro de valida√ß√£o */
.input-container--invalid .input {
	border: 1px solid var(--erro);      /* Adiciona uma borda s√≥lida de 1px com a cor de erro */
	border-radius: 7px;                 /* Adiciona bordas arredondadas ao campo de entrada */
}

/* Estilo para o r√≥tulo do campo de entrada quando h√° um erro de valida√ß√£o */
.input-container--invalid .input-label {
	color: var(--erro);                 /* Define a cor do texto do r√≥tulo como a cor de erro */
}

/* Estilo para a mensagem de erro abaixo do campo de entrada */
.input-message-error {
	display: none;                      /* Esconde a mensagem de erro por padr√£o */
}

/* Estilo para a mensagem de erro quando h√° um erro de valida√ß√£o */
.input-container--invalid .input-message-error {
	color: var(--erro);                 /* Define a cor do texto da mensagem de erro como a cor de erro */
	display: block;                     /* Exibe a mensagem de erro */
	margin-top: 0.5rem;                 /* Adiciona uma margem superior √† mensagem de erro */
	padding-left: 0.5rem;               /* Adiciona preenchimento √† esquerda da mensagem de erro */
}

/* Estilo padr√£o para o r√≥tulo do campo de entrada */
.input-label {
	position: absolute;                 /* Define a posi√ß√£o absoluta do r√≥tulo em rela√ß√£o ao cont√™iner pai */
	top: 0.5rem;                        /* Afasta o r√≥tulo da parte superior do campo de entrada */
	left: 0.5rem;                       /* Afasta o r√≥tulo da parte esquerda do campo de entrada */
	transition: all 0.25s;              /* Adiciona uma transi√ß√£o suave de 0.25 segundos para altera√ß√µes */
}

/* Estilo para o campo de texto quando h√° um erro de valida√ß√£o */
.input-container--invalid .textarea {
	border: 1px solid var(--erro);     /* Adiciona uma borda s√≥lida de 1px com a cor de erro */
}

/* Estilo para o r√≥tulo do campo de texto quando h√° um erro de valida√ß√£o */
.textarea-container--invalid .input-label {
