}

/* Estilo padr√£o para a mensagem de erro do campo de texto */
.textarea-mensagem-erro {
	display: none;                     /* Esconde a mensagem de erro por padr√£o */
}

/* Estilo para a mensagem de erro quando h√° um erro de valida√ß√£o no campo de texto */
.textarea-container--invalid .textarea-mensagem-erro {
	display: block;                    /* Exibe a mensagem de erro */
	color: var(--erro);                /* Define a cor do texto da mensagem de erro como a cor de erro */
	margin-top: 0.5rem;                /* Adiciona uma margem superior √† mensagem de erro */
