    /* Define a cor de fundo do bot√£o como a cor de destaque */
    font-weight: 500;
    /* Define o peso da fonte como 500 (moderadamente negrito) */
    border: none;
    /* Remove a borda do bot√£o */
    font-size: 1rem;
    /* Define o tamanho da fonte como 1rem */
