    /* Define a largura do bot√£o como 100% */
}

.boton-formulario-repproved:hover {
  box-shadow: 1px 1px 8px 0px var(--cor-de-realce);
  /* Adiciona uma sombra suave com cor de realce */
  cursor: pointer;
  /* Muda o cursor para uma m√£o indicando que o bot√£o √© clic√°vel */
}

.formulario-login__h2{
	font-size: 1.5rem;
	margin-bottom: 1.5rem;
}

/* ------------------------------------ */
/* ----- Responsive para Mobiles ------ */
