    /* Define a largura do formul√°rio como 70% da largura da tela */
  }
}

/* ------------------------------------ */
/* ----- Responsive para Tablets ------ */
/* ------------------------------------ */

/* Estilo aplicado apenas quando a largura da tela est√° entre 768 pixels e 1024 pixels (para tablets) */
@media (min-width:768px) and (max-width:1024px) {
  .formulario-approved_form {
    width: 70%;
    /* Define a largura do formul√°rio como 70% da largura da tela */
  }
}
